import { ReefFile, ReefFolder, ReefRepository } from "./ReefRepository";
import { AbiItem } from 'web3-utils'
import Web3 from 'web3';
import { Contract } from "web3-eth-contract"
import contractABI from './abi.json'
import axios from 'axios';

export class ReefRepositoryImplement implements ReefRepository {
    contractAddr = "0x5308132eb6826ef1fcc8b1f1f31f0675bc8fc646"
    web3: Web3;
    contract: Contract;
    nameResult: ReefFolder[] = [];
    nameLength = 0;
    detail: { [name: string]: ReefFile[] } = {};
    detailLength: { [name: string]: number } = {};
    constructor() {
        this.web3 = new Web3('wss://eth-goerli.g.alchemy.com/v2/1EUEe6ifJGvzlvwhRRhla2d0nAg5ZIvu');
        this.contract = new this.web3.eth.Contract( contractABI as AbiItem[], this.contractAddr);
    }
    
    async listFolders(): Promise<ReefFolder[]> {
        // reset memory data
        this.nameLength = 0
        
        
        let arrayLength = await this.contract.methods.getExcelArrayLength().call()
        let promiseArray = [];
        for (let i = this.nameLength; i <= arrayLength - 1; i++) {
            promiseArray.push(this.contract.methods.getExcelArrayItem(i).call());
        }
        let nameArray = await Promise.all(promiseArray);
        promiseArray = [];
        for (let i = this.nameLength; i <= arrayLength - 1; i++){
            // console.log(nameArray[0]["name"]);
            promiseArray.push(this.contract.methods.getExcelMapItem(nameArray[i]["name"],0).call());
        }
        let attrArray = await Promise.all(promiseArray);
        for (let i = this.nameLength; i <= arrayLength - 1; i++){
            let tmp = {
                path: nameArray[i - this.nameLength]["path"],
                name: nameArray[i - this.nameLength]["name"],
                size: +attrArray[i - this.nameLength]["size"],
                lastModified: new Date(+attrArray[i - this.nameLength]["time"] * 1000)
            }
            this.nameResult.push(tmp);
        }
        // console.log(this.nameResult);
        this.nameLength = arrayLength;
        return this.nameResult;
    }

    async listFiles(folderName: string): Promise<ReefFile[]> {
        // reset memory data
        this.detailLength[folderName] = 0;
        
        let arrayLength = await this.contract.methods.getexcelMapLength(folderName).call()

        let promiseArray = [];
        for (let i = this.detailLength[folderName]; i <= arrayLength - 1; i++) {
            promiseArray.push(this.contract.methods.getExcelMapItem(folderName, i).call());
        }
        let tmpArray = await Promise.all(promiseArray);

        for (let i = this.detailLength[folderName] ; i <= arrayLength - 1; i++){
            let tmp = {
                uuid: tmpArray[i - this.detailLength[folderName]]["cid"],
                name: folderName,
                size: +tmpArray[i - this.detailLength[folderName]]["size"],
                lastModified: new Date(+tmpArray[i - this.detailLength[folderName]]["time"] * 1000)
            }
            if (this.detailLength[folderName] === 0)
                this.detail[folderName] = [];    
            this.detail[folderName].push(tmp);
        }
        // console.log(this.detail[folderName]);
        this.detailLength[folderName] = arrayLength;
        return this.detail[folderName];
    }

    async downloadFile(fileUUID: string, fileName: string): Promise<void> {
        axios({
            url: 'https://cloudflare-ipfs.com/ipfs/' + fileUUID,
            method: 'GET',
            responseType: 'blob'
      })
            .then((response) => {
                  const url = window.URL
                        .createObjectURL(new Blob([response.data]));
                  const link = document.createElement('a');
                  link.href = url;
                  link.setAttribute('download', fileName);
                  document.body.appendChild(link);
                  link.click();
                  document.body.removeChild(link);
            })
    }
    
}
