 import { Alert, AlertColor, Snackbar } from '@mui/material';
import {
    ChonkyActions,
    ChonkyFileActionData,
    FileArray,
    FileBrowser,
    FileContextMenu,
    FileData,
    FileList,
    FileNavbar,
    FileToolbar,
    setChonkyDefaults,
} from 'chonky';
import { ChonkyIconFA } from 'chonky-icon-fontawesome';
import React, { useCallback, useEffect, useMemo, useState } from 'react';
import { ReefFile, ReefFolder, ReefRepository } from '../data/ReefRepository';

setChonkyDefaults({ iconComponent: ChonkyIconFA });

interface FolderMap {
    [folderPath: string]: FileData & { children?: string[], fileUUID?: string };
}

interface SnackBarInfo {
    message: string,
    severity: AlertColor
}

const InitFolderMap: FolderMap = {
    "/": {
        id: "/",
        name: "Root",
        isDir: true
    }
}

export const useFolderFiles = (folderPath: string, folderMap: FolderMap): FileArray => {
    return useMemo(() => {
        const currentFolder = folderMap[folderPath];
        const files = currentFolder.children
            ? currentFolder.children.map((children: string) => folderMap[children])
            : folderPath === "/"? [null]: [];
        
        return files;
    }, [folderPath, folderMap]);
};

export const useFolderChain = (folderPath: string, folderName: string | null, folderMap: FolderMap): FileArray => {
    return useMemo(() => {
        const pathChain = ["/"];

        if (folderPath !== "/") {
            for (let i = 1; i <= folderPath.length; ++i) {
                if (i === folderPath.length || folderPath.charAt(i) === '/') {
                    pathChain.push(folderPath.slice(0, i))
                }
            }
        }

        const folderChain = pathChain.map((path: string) => folderMap[path]);

        if (folderName != null) {
            folderChain.push({
                id: (folderPath === '/'? '': folderPath) + "/" + folderName,
                name: folderName + " (Multiple Versions)"
            })
        }
        return folderChain;
    }, [folderPath, folderName, folderMap]);
};

export const useFileActionHandler = (
    reefRepository: ReefRepository,
    isInFile: boolean,
    setSnackbarInfo: (snackbarInfo: SnackBarInfo) => void,
    setFolderPath: (folderPath: string) => void,
    setFolderName: (folderName: string | null) => void
) => {
    return useCallback(
        (data: ChonkyFileActionData) => {
            if (data.id === ChonkyActions.OpenFiles.id) {
                const { targetFile, files } = data.payload;
                const fileToOpen = targetFile ?? files[0];
                if (fileToOpen) {
                    if (fileToOpen.isDir) {
                        setFolderPath(fileToOpen.id);
                        setFolderName(null);
                    } else {
                        if (isInFile) {
                            setSnackbarInfo({
                                message: "The file is now downloading...",
                                severity: 'info'
                            });
                            reefRepository.downloadFile(fileToOpen.id, fileToOpen.name)
                        } else {
                            setFolderName(fileToOpen.name);
                        }
                    }
                }
            }

            console.log(data);
        },
        [setFolderPath, setFolderName, isInFile, reefRepository, setSnackbarInfo]
    );
};

export const initializeFolderMap = (
    reefRepository: ReefRepository,
    setFolderMap: (folderMap: FolderMap) => void,
    setSnackbarInfo: (snackbarInfo: SnackBarInfo) => void,
) => {
    reefRepository.listFolders()
        .then((reefFolders: ReefFolder[]) => {
            const folderMap: FolderMap = {
                "/": {
                    id: "/",
                    name: "Root",
                    isDir: true,
                    children: []
                }
            }

            reefFolders.forEach((reefFolder: ReefFolder) => {
                if (reefFolder.path !== '/') {
                    for (let i = 0; i <= reefFolder.path.length; ++i) {
                        if (i === reefFolder.path.length || reefFolder.path.charAt(i) === '/') {
                            const path = reefFolder.path.slice(0, i);
                            const name = path.slice(path.lastIndexOf("/") + 1);
                            const parent = path.lastIndexOf("/") === 0? "/":
                                            path.slice(0, path.lastIndexOf("/"));
                            
                            if (path.length > 0 && folderMap[path] === undefined) {
                                folderMap[parent].children?.push(path);
                                folderMap[path] = {
                                    id: path,
                                    name: name,
                                    children: [],
                                    isDir: true
                                }
                            }
                        }
                    }
                }

                const path = (reefFolder.path === '/'? '': reefFolder.path) + 
                                "/" + reefFolder.name;
                folderMap[reefFolder.path].children?.push(path);
                folderMap[path] = {
                    id: path,
                    name: reefFolder.name,
                    modDate: reefFolder.lastModified,
                    size: reefFolder.size,
                    isDir: false
                }
            });
            setFolderMap(folderMap);
        })
        .catch((err: any) => {
            setSnackbarInfo({
                message: err,
                severity: 'error'
            });
        });
}


export const updateVersionFiles = (
    reefRepository: ReefRepository,
    folderName: string | null,
    setVersionFiles: (versionFiles: FileArray | null) => void,
    setSnackbarInfo: (snackbarInfo: SnackBarInfo) => void,
) => {
    if (folderName == null) {
        setVersionFiles(null);
    } else {
        setVersionFiles([null]);
        reefRepository.listFiles(folderName)
            .then((reefFiles: ReefFile[]) => {
                const versionFiles: FileArray = reefFiles.map((reefFile: ReefFile) => {
                    return {
                        id: reefFile.uuid,
                        name: reefFile.name,
                        modDate: reefFile.lastModified,
                        size: reefFile.size,
                        isDir: false
                    }
                })

                setVersionFiles(versionFiles);
            })
            .catch((err: any) => {
                setSnackbarInfo({
                    message: err,
                    severity: 'error'
                });
            });
    }
}


export const ReefBrowser: React.FC<{ instanceId: string, reefRepository: ReefRepository }> = (props) => {
    const [snackbarInfo, setSnackbarInfo] = useState(null as (SnackBarInfo | null));
    
    const [folderPath, setFolderPath] = useState("/");
    const [folderName, setFolderName] = useState(null as (string | null));

    const [folderMap, setFolderMap] = useState(InitFolderMap);
    useEffect(() => {
        initializeFolderMap(props.reefRepository, setFolderMap, setSnackbarInfo);
    }, [props.reefRepository]);

    const folderFiles = useFolderFiles(folderPath, folderMap);

    const [versionFiles, setVersionFiles] = useState(null as (FileArray | null));
    useEffect(() => {
        updateVersionFiles(props.reefRepository, folderName, setVersionFiles, setSnackbarInfo);
    }, [folderName, props.reefRepository])

    const folderChain = useFolderChain(folderPath, folderName, folderMap);
    const handleFileAction = useFileActionHandler(
        props.reefRepository,
        folderName != null,
        setSnackbarInfo,
        setFolderPath,
        setFolderName
    );
    
    return (
        <div style={{ height: '80vh' }}>
            <FileBrowser
                instanceId={props.instanceId}
                files={versionFiles ?? folderFiles}
                folderChain={folderChain}
                onFileAction={handleFileAction}
                defaultFileViewActionId={ChonkyActions.EnableListView.id}
            >
                <FileNavbar />
                <FileToolbar />
                <FileList />
                <FileContextMenu />
            </FileBrowser>

            <Snackbar
                open={snackbarInfo !== null}
                autoHideDuration={3000}
                onClose={() => { setSnackbarInfo(null) }}
                ClickAwayListenerProps={{onClickAway: () => {}}}
            >
                <Alert
                    onClose={() => { setSnackbarInfo(null) }}
                    severity={snackbarInfo?.severity}
                    sx={{ width: '100%' }}
                >
                    {snackbarInfo?.message}
                </Alert>
            </Snackbar>
        </div>
    );
};